<script>
import PersonView from "../components/PersonViewComponent.vue";
import ClerkView from "../components/ClerkViewComponent.vue";
import CustomerView from "../components/CustomerViewComponent.vue";
import ReservationView from "../components/ReservationViewComponent.vue";
import VehicleView from "../components/VehicleViewComponent.vue";
import InstructionsView from "../components/InstructionsViewComponent.vue";
import ReservationSystemView from "../components/TestDriverComponent.vue";
import ClassView from "../components/ClassDiagramViewComponent.vue";

//rental
//test
export default {
  components: {
    CustomerView,
    ReservationView,
    VehicleView,
    InstructionsView,
    ReservationSystemView,
    ClassView,
    PersonView,
    ClerkView,
  },
  data() {
    return {
      componentSelect: "InstructionsView",
      testdriver_completed: false,
    };
  },
  methods: {
    testDriverCallBack(value){
      console.log("Test driver callback triggered", value);
      this.testdriver_completed = true;
    },
    instructionsReadCallback(value) {
      console.log("Instructions read", value);
      this.componentSelect = 'ReservationSystemView'
    }
  },
};
</script>

<template>
  <main class="app">
    <button @click="componentSelect = 'InstructionsView'" :style="componentSelect === 'InstructionsView' ? 'background-color: #55acee' : ''">1. Instructions</button>
    <button @click="componentSelect = 'ClassView'" :style="componentSelect === 'ClassView' ? 'background-color: #55acee' : ''">2. Class diagram</button>
    <button @click="componentSelect = 'PersonView'" :style="componentSelect === 'PersonView' ? 'background-color: #55acee' : ''">3. Person Class</button>
    <button @click="componentSelect = 'CustomerView'" :style="componentSelect === 'CustomerView' ? 'background-color: #55acee' : ''">4. Customer Class</button>
    <button @click="componentSelect = 'ClerkView'" :style="componentSelect === 'ClerkView' ? 'background-color: #55acee' : ''">5. Exec Class</button>
    <button @click="componentSelect = 'ReservationView'" :style="componentSelect === 'ReservationView' ? 'background-color: #55acee' : ''">
      6. Reservation Class
    </button>
    <button @click="componentSelect = 'VehicleView'" :style="componentSelect === 'VehicleView' ? 'background-color: #55acee' : ''">7. Vehicle Class</button>
    <button @click="componentSelect = 'ReservationSystemView'" :style="componentSelect === 'ReservationSystemView' ? 'background-color: #55acee' : ''">
      8. Test Driver Class
    </button>
    
    <KeepAlive>
      <component
        v-bind:is="componentSelect"
        @testdriver-complete="testDriverCallBack"
        @instructions-read="instructionsReadCallback"
      />
    </KeepAlive>
  </main>
  <footer v-show="(testdriver_completed)">
    <div id="buttons" class="relative">
      <button class="navitem" @click="$router.push('/method-completion')">Next</button>
    </div>
  </footer>
</template>

<style>
.app {
  background-color: #f9fafe;
  min-height: 1024px;
}

button {
  margin-top: 30px;
  border: none;
  padding: 20px;
  margin-left: 5px;
  background-color: #2c3e50;
  color: white;
}

button:first-of-type {
  margin-left: 30px;
}
</style>
